
	import {
		imConfig
	} from './util/config.js'
	const Services = require('./util/services.js');
	const RongIMLib = require('./lib/RongIMLib.wx-1.2.0.js');
	export default {
		globalData: {
			Services: Services
		},
		data() {
			return {
				data: {
					appKey: 'bmdehs6pbguas',
					token: wx.getStorageSync('token'),
				},
			}
		},
		computed: {
			userInfo() {
				return this.$store.state.userInfo
			}
		},

		// 应用生命周期仅可在App.vue中监听，在其它页面监听无效。
		onLaunch: function() {

			let that = this

			wx.setEnableDebug({
				enableDebug: true
			});
			if (uni.getStorageSync('token')) {
				
				if (uni.getStorageSync('userInfo')) {
					that.$store.dispatch('setLoginStatus', 1)
					that.$store.dispatch('setUserInfo', uni.getStorageSync('userInfo'))
					// } else {
					// that.$store.dispatch('setLoginStatus', 1)
					// that.$store.dispatch('setUserInfo', uni.getStorageSync('userInfo'))
				}
			} else {
				that.$store.dispatch('setLoginStatus', 0);
				that.$store.dispatch('logout');
			}
      
      console.log('loading login status.......', this.$store.state.loginStatus)


			if (wx.canIUse('getUpdateManager')) {
				const updateManager = wx.getUpdateManager()
				updateManager.onCheckForUpdate(function(res) {
					console.log('onCheckForUpdate====', res)
					// 请求完新版本信息的回调
					if (res.hasUpdate) {
						console.log('res.hasUpdate====')
						updateManager.onUpdateReady(function() {
							wx.showModal({
								title: '更新提示',
								content: '新版本已经准备好，是否重启应用？',
								success: function(res) {
									console.log('success====', res)
									// res: {errMsg: "showModal: ok", cancel: false, confirm: true}
									if (res.confirm) {
										// 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
										updateManager.applyUpdate()
									}
								}
							})
						})
						updateManager.onUpdateFailed(function() {
							// 新的版本下载失败
							wx.showModal({
								title: '已经有新版本了哟~',
								content: '新版本已经上线啦~，请您删除当前小程序，重新搜索打开哟~'
							})
						})
					}
				})
			}


			this.checkLogin();
			this.getCategory();
		},

		methods: {
			/**
			 * 检查session 是否已经登录
			 */
			checkLogin() {
				let that = this;
				// uni.showLoading({title:"加载中"});
				uni.login({
					provider: "weixin",
					success: (loginRes) => {
						// uni.hideLoading();
						if (loginRes && loginRes.code) {
							that.$http.getSessionToken({
									code: loginRes.code
								})
								.then(res => {
									if (res && res.code == 200 && res.data && res.data.token) {
										let userInfo = {
											avatarUrl: res.data.avatarUrl,
											isSubscribe: res.data.isSubscribe,
											nickName: res.data.nickName,
											userId: res.data.userId
										};
										that.avatarUrl = userInfo.avatarUrl;
										that.nickName = userInfo.nickName;
										that.$store.commit('setToken', res.data.token);
										that.$store.commit('setUserInfo', userInfo);
										that.$store.commit('setLoginStatus', 1);
										that.initSDK(that);
									}
								})
						}
					}
				})
			},

			initSDK(context) {

				const self = this

				let appKey = context.data.appKey;
				let token = context.data.token;

				if (appKey == '' || token == '') {
					// return showPrompt('必须提供 appKey 和 token');
				}

				this.$http
					.imUserRegister({
						id: self.userInfo.userId,
						name: self.userInfo.nickName,
						portraitUri: self.userInfo.avatarUrl,
					})
					.then(res => {
						console.log(res)

						// 公有云初始化
						let modules = {
							//protobuf: 'https://cdn.ronghub.com/protobuf-2.3.1.min.js' //支持http(s)网络路径、本地相对路径,
							RongIMLib: RongIMLib,
							protobuf: null
						};

						let params = {
							appKey: appKey,
							token: token,
							connectToken: res.data.token
						};

						let callbacks = {
							receiveNewMessage: function(message) {
								uni.$emit('newMessage', message)
							},
							getCurrentUser: function(userInfo) {},
							onTokenIncorrect: function() {
								console.log("token错误重新连接")
							},
							networkError(msg) {
								self.globalData.Services.Status.reconnect();
							},
							watchConversationList: function(list) {
								console.log(list)
							}
						}
						this.globalData.Services.init(params, callbacks, modules);
					})
					.catch(err => {
						console.log(err)
					})
			},

			/**
			 * 获取categorys
			 */
			getCategory() {
				if (!uni.getStorageSync("catalogues")) {
					this.$http.getCategory({}).then(res => {
						this.$store.commit('setCatalogues', res.data);
					});
				}

			},

		}

	}
