import chatItem from '../../components/chat-item/chat-item.vue'
import chatInput from '../../components/chat-input/chat-input.vue'
import app from '../../App.vue'
export default {
  components: {
    chatItem,
    chatInput
  },
  data() {
    return {
      toLast: '',
      name: '',
      text: '',
      headUrl: '',
      chatList: [],
      imgList: [],
      targetId: '',
      scrollTop: '',
      // 聊天页面时时滚动样式
      style: {
        pageHeight: 0,
        contentViewHeight: 0,
        footViewHeight: 90,
        mitemHeight: 0
      }
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo
    }
  },
  onLoad() {
    const self = this
    
    this.targetId = this.$parseURL().targetId
    this.avatarUrl = this.$parseURL().avatarUrl
    this.userName = this.$parseURL().userName
    
    console.log(this.targetId)
    console.log(this.avatarUrl)
    console.log(this.userName)

    uni.setNavigationBarTitle({
      title: this.userName 
    })

    this.loadHistoryMsg()


    // 清除未读
    app.globalData.Services.Conversation.clearUnreadCount(function(res) {
      console.log(res)
    }, {
      targetId: this.targetId
    })
    
    
    // 事件触发
    uni.$on('newMessage', function(res) {
      // 判断新消息是不是当前对象
      if(self.targetId == res.targetId){
        // app.globalData.Services.Conversation.clearUnreadCount(function(res) {
        //   console.log(res)
        // }, {
        //    targetId: self.targetId
        // })
        if (res.messageDirection === 1) {
          res.isMy = true
          res.headUrl = self.userInfo.avatarUrl
        } else {
          res.isMy = false
          res.headUrl = self.avatarUrl
        }
        
        console.log(res)
        
        self.chatList.push(res)
        
         // 滚动
        self.toLast = 'item' + self.chatList.length
      }else{
        console.log(self.targetId)
        console.log(res.targetId)
     
      }
 
      
    })
    
  },
  methods: {
    loadHistoryMsg(){
      const self = this
      
      // 历史聊天记录获取
      app.globalData.Services.Message.getHistory(function(res) {
        console.log(res)
      
        self.chatList = res
        self.chatList.forEach((item) => {
          if (item.messageDirection === 1) {
            item.isMy = true
            item.headUrl = self.userInfo.avatarUrl
          } else {
            item.isMy = false
            item.headUrl = self.avatarUrl
          }
          
          self.createImgList(item)
        })
        
        // 滚动
        self.toLast = 'item' + self.chatList.length
      }, {
        targetId: this.targetId
      })
    },
    
    
    createImgList(item) {
      if (item.content.messageName === 'ImageMessage') {
        this.imgList.push(item.content.imageUri)
      }
      // console.log(this.imgList)
    },

    // 发生文本消息
    handleSendMsgFunc(info) {
      const self = this
      
      // 查询对方用户信息
      self.$http.imUserGet({
        id: this.targetId
      }).then(result => {
      
        app.globalData.Services.Message.sendText(function(res) {
          if (res.messageDirection === 1) {
            res.isMy = true
            res.headUrl = self.userInfo.avatarUrl
          } else {
            res.isMy = false
            res.headUrl = self.avatarUrl
          }
          self.chatList.push(res)
          self.createImgList(res)
          
          // 滚动
          self.toLast = 'item' + self.chatList.length
        }, {
          targetId: self.targetId,
          content: info
        },{
          id:result.data.userId,
          portrait :result.data.userPortrait,
          name :result.data.userName,
          meid:self.userInfo.userId,
          meportrait :self.userInfo.avatarUrl,
          mename :self.userInfo.nickName,
        })               
      })
      
      
    },
    
    // 发送图片消息
    handleSendImgMsgFunc(imgUrl) {
      const self = this

      // 查询对方用户信息
      self.$http.imUserGet({
        id: this.targetId
      }).then(result => {
      
        app.globalData.Services.Message.sendImage(function(res) {
          if (res.messageDirection === 1) {
            res.isMy = true
            res.headUrl = self.userInfo.avatarUrl
          } else {
            res.isMy = false
            res.headUrl = self.avatarUrl
          }
          self.chatList.push(res)
          self.createImgList(res)
          
          // 滚动
          self.toLast = 'item' + self.chatList.length
        }, {
          targetId: self.targetId,
          url: imgUrl
        },{
          id:result.data.userId,
          portrait :result.data.userPortrait,
          name :result.data.userName,
          meid:self.userInfo.userId,
          meportrait :self.userInfo.avatarUrl,
          mename :self.userInfo.nickName,
        })    
      })
    }
    
    
  }
}
